label {
  color: #fff;
  margin-bottom: 0.5rem;
}

.form {
  background: linear-gradient(to right, rgb(1 0 4), rgb(1 3 42));
}

form {
  display: flex;
  flex-direction: column;
  padding: 4rem 1rem;
  margin: auto;
  max-width: 600px;
}

form input,
form textarea {
  margin-bottom: 1rem;
  padding: 10px 18px;
  font-size: 1.2rem;
  background-color: rgba(255, 255, 255, 0.1);
  border-color: rgba(255, 255, 255, 0);
  color: #f4f4f4;
}
